import React, { useState } from "react";
import {
  Upload,
  Button,
  Icon,
  message,
  Progress,
  Input,
  Typography
} from "antd";
// import { updateProfile } from "../../../action/auth";
// import { useDispatch } from "react-redux";

import axio from "axios";
const { Title } = Typography;
const ProfileUpdate = props => {
  const [file, setFile] = useState(null);
  const [name, setName] = useState("");
  const [uploading, setUploading] = useState(false);
  const [uploadPercent, setUploadPercent] = useState(0);
  //   const dispatch = useDispatch();
  const handleUpload = () => {
    const formData = new FormData();

    formData.append("avatar", file);
    formData.append("name", name);

    setUploading({
      uploading: true
    });

    console.log(name);

    // You can use any AJAX library you like
    axio
      .post("/user/editProfile", formData, {
        onUploadProgress: progressEvent => {
          var percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          setUploadPercent(percentCompleted);
        }
      })
      .then(res => {
        window.location.reload(false);
        setUploading(false);
        setFile(null);
        setName("");
        setUploadPercent(0);
        message.info("profile updated");
      })
      .catch(() => {
        message.error("profile updated");
      });
  };

  const uploadSetting = {
    onRemove: file => {
      setFile(null);
    },
    beforeUpload: file => {
      setFile(state => file);
      return false;
    }
  };

  return (
    <div
      style={{
        border: "1px solid lightgray",
        margin: " 0 8px",
        marginBottom: "16px",
        height: "440px",
        borderRadius: "16px"
      }}
    >
      <div
        style={{
          padding: "8px",
          height: "90%",
          margin: "0 auto",
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
          alignItems: "center"
        }}
      >
        <div>
          <div>
            <Title level={2}>Update Profile</Title>
            <br />
            <br />
            <Input
              placeholder="Update your Name"
              value={name}
              onChange={e => setName(e.target.value)}
              style={{
                width: "190px"
              }}
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
            />

            <br />
            <br />
          </div>
          <Upload
            {...uploadSetting}
            multiple={false}
            style={{ width: "240px" }}
          >
            <Button>
              <Icon type="upload" /> Select New Profile pic{"  "}
            </Button>
          </Upload>
        </div>

        {/* {uploading ? ( */}
        <Progress type="circle" percent={uploadPercent} />
        {/* ) : <> </>} */}
        <Button
          type="primary"
          onClick={handleUpload}
          disabled={name.length === 0 || file === null}
          loading={uploading}
          style={{ marginTop: 16 }}
        >
          {uploading ? "Uploading" : "Update Profile"}
        </Button>
      </div>
    </div>
  );
};

export default ProfileUpdate;
