import React from "react";
import { Row, Col, Button } from "antd";
import ProfileUpdate from "./ProfileUpdate";
import { Typography } from "antd";
import PasswordUpdate from "./PasswordUpdate";
import useTitle from "../../util/useTitle";
const { Title } = Typography;
const Index = props => {
  useTitle("info");
  const Item = () => (
    <Col
      xs={24}
      sm={12}
      md={12}
      xl={8}
      xxl={6}
      style={{ marginBottom: "16px" }}
    >
      <div
        style={{
          height: "50px",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          width: "90%",
          margin: "0 auto",
          backgroundColor: "white",
          borderRadius: "8px",
          boxShadow: "0px 0px 10px #cccccc"
        }}
      >
        {" "}
        <p style={{ marginLeft: "8px", paddingTop: "4px" }}>
          <b> name of project</b>{" "}
        </p>
        <Button style={{ marginRight: "8px" }} type="danger">
          Leave
        </Button>
      </div>
    </Col>
  );

  return (
    <div style={{ width: "94%", margin: "0 auto" }}>
      <Row>
        <Col xs={24} sm={12}>
          <ProfileUpdate />
        </Col>
        <Col xs={24} sm={12}>
          <PasswordUpdate />
        </Col>
        <Col xs={24}>
          <Row>
            <div
              style={{
                border: "1px solid lightgray",
                margin: " 0 8px",
                marginBottom: "16px",
                height: "440px",
                borderRadius: "16px"
              }}
            >
              <Title
                level={2}
                style={{ textAlign: "center", paddingTop: "8px" }}
              >
                All Projects
              </Title>

              {Item()}
              {Item()}
              {Item()}
              {Item()}
              {Item()}
            </div>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default Index;
